html,
body {
    scroll-behavior: smooth;
}
button {
    cursor: pointer;
}
#main {
    * {
        font-family:
            "DM Sans",
            "Pretendard",
            "Noto Sans KR",
            -apple-system,
            BlinkMacSystemFont,
            "Segoe UI",
            Roboto,
            "Helvetica Neue",
            Arial,
            sans-serif !important;
    }
    .material-icons {
        font-family: "Material Icons" !important;
    }

    .hidden {
        display: none !important;
    }
    main {
        section {
            position: relative;
            padding: 130px 0;
            width: 100%;
            min-height: 100dvh;
        }
        .section-title {
            margin-bottom: 60px;
            width: fit-content;
            @include heading1;
            @include text-gradient;
            font-weight: bold;
            &.text-center {
                margin-left: auto;
                margin-right: auto;
                text-align: center;
                & + .sub-title {
                    text-align: center;
                }
            }
            small {
                display: block;
                margin-bottom: 12px;
                @include sub-title;
                color: #767676;
                display: none;
            }

            & + .sub-title {
                margin-top: -40px;
                margin-bottom: 60px;
                // text-align: center;
                @include body2;
                color: $gray900;
            }
        }
        .title-box {
            display: flex;
            align-items: flex-end;
            h2,
            p {
                flex: 1;
            }
        }

        //section1
        #section1 {
            padding: 0;
            overflow: hidden;
            .main-visual {
                position: relative;
                aspect-ratio: 1920 / 880;
                background: url("../img/main_visual_bg2.png") no-repeat center top / contain;
                overflow: hidden;

                .main-text {
                    margin-top: 162px;
                    @include hero;
                    color: #fff;
                }

                .mockup {
                    position: absolute;
                    right: 11%;
                    bottom: -51%;
                }
            }
            .main-quote {
                position: relative;
                display: flex;
                padding: 44px 0 42px;
                align-items: center;
                justify-content: center;
                text-align: center;
                background: #fff;
                dt {
                    width: fit-content;
                    margin: 0 auto 16px;
                    @include heading2;
                    @include text-gradient;
                }
                dd {
                    @include body2;
                    color: $gray600;
                }
            }
            .partner-wrap {
                padding: 120px 0 40px;
                text-align: center;
                h3 {
                    margin-bottom: 40px;
                    @include heading2;
                    font-weight: 300;
                    color: $gray600;
                    strong {
                        font-weight: bold;
                        color: $black;
                    }
                }
            }
        }

