        //section3
        #section3 {
            background: linear-gradient(0deg, rgba(161, 225, 242, 0.1) 0%, rgba(161, 225, 242, 0.1) 100%),
                linear-gradient(0deg, rgba(43, 214, 169, 0) 0%, rgba(43, 214, 169, 0.2) 100%), #fff;
            .card-wrap {
                display: flex;
                gap: 30px;
                .col {
                    border-radius: 40px;
                    &-left {
                        flex: 2;
                    }
                    &-center {
                        flex: 1;
                    }
                    &-right {
                        flex: 1;
                        display: flex;
                        flex-flow: column;
                        gap: 30px;
                    }
                }

                .item {
                    position: relative;
                    perspective: 2000px;
                    height: 100%;
                    img {
                        opacity: 1;
                        transform: rotate3d(0, 0, 0, 180deg);
                        transform-style: preserve-3d;
                        border-radius: 40px;
                        height: 100%;
                        object-fit: cover;
                        transition: all 0.6s ease;
                    }
                    .txt-box {
                        opacity: 0;
                        transform: rotate3d(0, 0.5, 0, 180deg);
                        transform-style: preserve-3d;
                        position: absolute;
                        left: 0;
                        top: 0;
                        width: 100%;
                        height: 100%;
                        padding: 40px;
                        border-radius: 40px;
                        transition: all 0.6s ease;
                        h3 {
                            margin-bottom: 20px;
                            @include heading2;
                        }
                        p {
                            @include body2;
                            opacity: 0.8;
                        }
                    }

                    &:hover {
                        .txt-box {
                            opacity: 1;
                            transform: rotate3d(0, 0, 0, 180deg);
                        }
                        img {
                            opacity: 0;
                            transform: rotate3d(0, -0.5, 0, 180deg);
                        }
                    }
                    &.item1 .txt-box {
                        background: #f9ce78;
                    }
                    &.item2 .txt-box {
                        color: #fff;
                        background: #4d4e4d;
                    }
                    &.item3 .txt-box {
                        color: #fff;
                        background: #6479db;
                    }
                    &.item4 .txt-box {
                        background: #a9db65;
                    }
                }
            }
        }

        //section4
        #section4 {
            min-height: 0;
            overflow-x: hidden;
            background: url("../img/gradient_bubble_bg.png") no-repeat center top / 100% auto;
            background-attachment: fixed;

            .flywheel-wrap {
                position: relative;
                aspect-ratio: 1507/1028;
                width: 1507px;
                max-width: 100%;
                margin-left: auto;
                margin-right: auto;

                .flywheel-img {
                    z-index: 10;
                    position: relative;
                }
                .txt-box {
                    z-index: 10;
                    position: absolute;
                    left: 0;
                    top: 0;
                    width: 250px;
                    h3 {
                        margin-bottom: 8px;
                    }

                    &.text-right {
                        text-align: right;
                        h3 {
                            margin-left: auto;
                        }
                    }
                    &.img-title {
                        left: 11.9%;
                        top: 18.3%;
                        h3 {
                            color: #3f3f3f;
                            font-size: 18px;
                            font-weight: 700;
                            line-height: 120%; /* 21.6px */
                            letter-spacing: -0.36px;
                            text-transform: uppercase;
                        }
                        p {
                            color: $gray900;
                            @include caption1;
                        }
                    }
                    &.type1 {
                        h3 {
                            width: fit-content;
                            @include text-gradient;
                            @include heading2;
                        }
                        p {
                            @include body2;
                            color: $gray900;
                        }
                    }
                    &.type2 {
                        h3 {
                            @include sub-title;
                            color: #3f3f3f;
                        }
                        p {
                            @include body2;
                            color: $gray900;
                        }
                    }

                    &.box1 {
                        width: 290px;
                        left: 78.7%;
                        top: 22.3%;
                    }
                    &.box2 {
                        width: 290px;
                        left: 0.4%;
                        top: 51.3%;
                    }
                    &.box3 {
                        width: 250px;
                        left: 81.5%;
                        top: 48%;
                    }
                    &.box4 {
                        width: 250px;
                        left: 74%;
                        top: 68.1%;
                    }
                    &.box5 {
                        width: 350px;
                        left: 0%;
                        top: 66.5%;
                    }
                    &.box6 {
                        width: 390px;
                        left: 3.8%;
                        top: 35.7%;
                    }
                }
            }
        }

        //section5
        #section5 {
            background: url(../img/teacher_app_bg.png) no-repeat center bottom / cover;
            background-attachment: fixed;

            .teacher-wrap {
                display: flex;
                gap: 32px;
                .item {
                    flex: 1;
                    position: relative;
                    aspect-ratio: 794 / 668;
                    padding: 52px 75px;

                    &:nth-child(1) {
                        background: url(../img/teacher_box1.png) no-repeat center / contain;
                    }
                    &:nth-child(2) {
                        background: url(../img/teacher_box2.png) no-repeat center / contain;
                    }
                }
                h3 {
                    position: relative;
                    margin-bottom: 12px;
                    @include heading2;
                    color: $black;

                    .icon {
                        position: absolute;
                        right: 100%;
                        top: -2px;
                        margin-right: 8px;
                    }
                }
                p {
                    width: 290px;
                    @include body2;
                    color: $gray900;
                }
            }
        }

        //section6
        #section6 {
            background: url("../img/user_flow_bg.png") no-repeat center / cover;
            // background-attachment: fixed;
            .mobile-screens {
                position: relative;
                top: 32px;
                transition: all 0.6s ease;
                .inner {
                    display: flex;
                    gap: 16px;
                    height: fit-content;
                    > div {
                        flex: 1;
                        display: flex;
                        flex-flow: column;
                        gap: 16px;

                        &:nth-child(1),
                        &:nth-child(4) {
                            padding-top: 40px;
                        }
                    }
                }

                .screen {
                    border-radius: 12px;
                    background: $bg-light;
                    aspect-ratio: 1 / 1;
                    padding: 20px;
                    transition: all 0.6s ease;
                    &:nth-child(even) {
                        margin-top: 20px;
                    }

                    &.main-screen {
                        height: 650px;
                        position: absolute;
                        left: 50%;
                        top: 150px;
                        transform: translateX(-50%);
                        aspect-ratio: 333 / 692;
                        background: $bg-medium;
                        box-shadow: 0 0 20px rgba(0, 0, 0, 0.1);
                        // transition: none;
                        margin-top: 0;
                    }
                }

                &.active {
                    top: 12px;
                    .screen {
                        margin-top: 0;

                        &.main-screen {
                            top: -40px;
                        }
                    }
                }
                &.deeper {
                    top: 0px;
                    .screen {
                        margin-top: 0px;

                        &.main-screen {
                            top: -100px;
                        }
                    }
                }
            }
        }

        //section7
        #section7 {
            padding: 0;
            aspect-ratio: 1920 / 1080;
            overflow: hidden;
            background: url("../img/color_bg.png") no-repeat center / cover;
            .container {
                position: relative;
                height: 100%;

                .img-box {
                    position: absolute;
                    left: 0px;
                    bottom: -120px;
                    width: 100%;

                    .screen {
                        z-index: 20;
                        position: relative;
                        left: 20px;
                    }
                    .component-list {
                        z-index: 10;
                        position: absolute;
                        right: 0;
                        top: 80px;
                        display: flex;
                        gap: 16px;
                        width: 1170px;
                    }
                }

                .txt-box {
                    position: absolute;
                    right: 0;
                    bottom: 130px;
                    width: 840px;
                    max-width: 100%;

                    p {
                        column-count: 2;
                        column-gap: 24px;
                        width: 774px;
                        max-width: 100%;
                    }

                    .color-list {
                        display: flex;
                        gap: 12px;

                        li {
                            display: flex;
                            flex-flow: column;
                            text-align: center;
                            border: 1px solid $l-regular;
                            flex-basis: 100px;
                            &::before {
                                content: "";
                                margin-bottom: 16px;
                                display: block;
                                width: 100%;
                                aspect-ratio: 1/1;
                                background: #efefef;
                            }
                            &.c1::before {
                                background: #ff5c5c;
                            }
                            &.c2::before {
                                background: #f7b305;
                            }
                            &.c3::before {
                                background: #80c720;
                            }
                            &.c4::before {
                                background: #00babb;
                            }
                            &.c5::before {
                                background: #5b89ff;
                            }

                            .name {
                                display: block;
                                margin-bottom: 4px;
                                @include caption1;
                                color: $black;
                            }
                            .value {
                                display: block;
                                margin-bottom: 16px;
                                @include caption2;
                                color: $gray900;
                            }
                        }
                    }
                }
            }
        }
        //section8
        #section8 {
            aspect-ratio: 1920/1080;
            background: url("../img/overview.png") no-repeat center / cover;
            background-attachment: fixed;
        }

        //section9
        #section9 {
            background: url("../img/design_guide_bg.png") no-repeat center / cover;
            .design-wrap {
                display: flex;
                flex-flow: column;
                gap: 64px;

                h3 {
                    margin-bottom: 20px;
                    @include sub-title;
                    color: #767676;
                }
                .row {
                    display: flex;
                    align-items: flex-start;
                    gap: 56px;
                }
                .color-wrap {
                    flex-basis: 71%;
                }
                .icon-wrap {
                    flex-basis: 29%;
                }
                .module-color-wrap {
                    flex-basis: 53%;
                }
                .typo-graphic {
                    flex-basis: 47%;
                    display: flex;
                    flex-flow: column;
                    gap: 80px;
                    justify-content: space-between;
                }
                .g-color-wrap {
                    position: relative;
                    &::after {
                        content: "";
                        display: block;
                        position: absolute;
                        right: 0;
                        top: 0;
                        width: 20%;
                        height: 100%;
                    }
                }
            }
        }
    }

    #footer {
        padding: 12px 0;
        background: $bg-regular;
        .copy {
            @include caption1;
            color: $gray600;
        }
    }
}
